.Main-style3 {
    flex-wrap: wrap;
}

.Main-title3 {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 92%;
    margin: auto;
}

.Main-title3 > h2 {
    display: flex;
    justify-content: center;
    align-items: center;
}

.Main3-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto;
}


.Box3 {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    
    padding: 2em;
}

.Text-container3 {
    display: flex;
    flex-direction: column;
}

.Box3-id-1 {
    grid-column: 1;
    grid-row: 1;
    border-bottom: 1px solid black;
}

.Box3-id-2 {
    grid-column: 2;
    grid-row: 1;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
}

.Box3-id-3 {
    width: 62%;
    grid-column: 1;
    grid-row: span 2;
}

.Box3-id-3 .Circle3 {
    display: none;
}

.Box3-id-4 {
    grid-column: 2;
    grid-row: 2;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
}

.Box3-id-5 {
    grid-column: 2;
    grid-row: 3;
    border-left: 1px solid black;
    border-bottom: 1px solid black;
}





.Circle3 {
    position: relative;
    height: 9em;
    width: 9em;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-right: 1em;
    animation: fillCircle 2s ease forwards;
    --percentage: 0deg;

    background: conic-gradient(var(--color, #00bfff) var(--percentage, 0deg), #eee 0deg);
}



.Circle3::before {
    content: '';
    position: absolute;
    top: 10%;
    left: 10%;
    width: 80%;
    height: 80%;
    background-color: white;
    border-radius: 50%;
    z-index: 1;
}

.Circle3 > h4 {
    font-size: 40px;
    font-weight: 100;
    position: relative;
    z-index: 2;
    color: blue;
}





.Box3-id-1 .Main3-h3 {
    display: none;
}

.Box3-id-2 .Main3-h3, .Box3-id-4 .Main3-h3, .Box3-id-5 .Main3-h3 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 0;
}

.Box3-id-2 .Label3, .Box3-id-4 .Label3, .Box3-id-5 .Label3 {
    font-size: 30px;
    font-weight: light;
    margin-top: 0.2em;
}

.Box3-id-1 .Label3 {
    font-size: 30px;
    font-weight: light;
    text-align: center;
}

.Box3-id-3 .Main3-h3 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 0em;
}

.Box3-id-3 .Label3 {
    font-size: 30px;
    font-weight: light;
    margin-top: 0em;
}
















@media (max-width: 1850px) {
    .Main-style3 {
        width: 100%;
        height: 100%;
        justify-content: space-around;
        gap: 2em;
    }
    .Main-title3 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 95%;
        margin: auto;
    }
    .Main-title3 > h2 {
        font-size: 22px;
    }
    .Box3 {
        padding-top: 2.5em;
        padding-bottom: 2.5em;
    }
    .Box3-id-3 {
        width: 90%;
    }
    .Circle3 {
        height: 7.5em;
        width: 7.5em;
    }
    .Circle3::before {
        top: 10%;
        left: 10%;
        width: 80%;
        height: 80%;
    }
    

    .Box3-id-1 .Main3-h3 {
        display: none;
    }

    .Box3-id-2 .Main3-h3, .Box3-id-4 .Main3-h3, .Box3-id-5 .Main3-h3 {
        font-size: 16px;
    }

    .Box3-id-2 .Label3, .Box3-id-4 .Label3, .Box3-id-5 .Label3 {
        font-size: 22px;
    }

    .Box3-id-1 .Label3 {
        font-size: 22px;
    }

    .Box3-id-3 .Main3-h3 {
        font-size: 16px;
    }

    .Box3-id-3 .Label3 {
        font-size: 30px;
    }

    .Circle3 > h4 {
        font-size: 36px;
    }
}

@media (max-width: 1300px) {
    .Main-style3 {
        width: 100%;
        height: 100%;
        
    }
    .Main-title3 > h2 {
        font-size: 18px;
        font-weight: 600;
    }
    .Main-title3 > p {
        font-size: 12px;
    }
    .Box3 {
        padding: 1em;
    }
    .Circle3 {
        height: 6em;
        width: 6em;
    }
    .Circle3::before {
        top: 10%;
        left: 10%;
        width: 80%;
        height: 80%;
    }

    .Box3-id-2 .Main3-h3, .Box3-id-3 .Main3-h3, .Box3-id-4 .Main3-h3, .Box3-id-5 .Main3-h3 {
        font-size: 12px;
    }

    .Box3-id-1 .Label3, .Box3-id-2 .Label3, .Box3-id-3 .Label3, .Box3-id-4 .Label3, .Box3-id-5 .Label3 {
        font-size: 18px;
    }

    .Circle3 > h4 {
        font-size: 30px;
    }
}



@media (max-width: 1000px) {
    .Main-style3 {
        width: 100%;
        height: 100%;
    }
    .Main-title3 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 95%;
        margin: auto;
    }
    .Main-title3 > h2 {
    font-size: 16px;
    font-weight: 600;
    }
    .Main-title3 > p {
    font-size: 12px;
    }
    .Box3 {
        padding: 1.5em;
        flex-direction: column;
        text-align: center;
    }
    .Box3-id-3 {
        text-align: start;
    }
    .Circle3 {
        height: 5em;
        width: 5em;
        margin: 0em;
    }
    .Circle3::before {
        top: 10%;
        left: 10%;
        width: 80%;
        height: 80%;
    }

    .Box3-id-2 .Main3-h3, .Box3-id-3 .Main3-h3, .Box3-id-4 .Main3-h3, .Box3-id-5 .Main3-h3 {
        font-size: 10px;
    }

    .Box3-id-1 .Label3, .Box3-id-2 .Label3, .Box3-id-3 .Label3, .Box3-id-4 .Label3, .Box3-id-5 .Label3 {
        font-size: 14px;
    }
    
    .Circle3 > h4 {
        font-size: 22px;
    }
}

@media (max-width: 850px) {
    .Main-style3 {
        width: 100%;
        height: 100%;
        flex-direction: column;
        flex-wrap: nowrap;
        align-items: flex-start;
    }

    .Main-title3 {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        width: 95%;
        margin: auto;
    }

    .Main-title3 > h2 {
        font-size: 14px;
        font-weight: 600;
        flex-direction: column;
        align-items: flex-start;
    }
    .dynamicHeading {
        margin-left: 0em;
    }

    .Box3 {
        padding: 1em;
    }
    .Box3-id-3 {
        justify-content: center;
    }
    .Circle3 {
        height: 4em;
        width: 4em;
        margin-bottom: .5em;
    }
    .Circle3::before {
        top: 10%;
        left: 10%;
        width: 80%;
        height: 80%;
    }

    .Box3-id-2 .Main3-h3, .Box3-id-3 .Main3-h3, .Box3-id-4 .Main3-h3, .Box3-id-5 .Main3-h3 {
        font-size: 8px;
    }

    .Box3-id-1 .Label3, .Box3-id-2 .Label3, .Box3-id-3 .Label3, .Box3-id-4 .Label3, .Box3-id-5 .Label3 {
        font-size: 12px;
    }
    
    .Circle3 > h4 {
        font-size: 22px;
    }
}